<template>
  <div class="test">
    <div class="page">
      <div class="container">
        <div class="left">
          <div class="login">Login</div>
          <div class="eula">
            Love and wisdom，the two can not have both
            <br>
            <br>
            <span id="text" v-if="count > 5"  style="color: #61C3FF;">爱与智慧，两者不可兼得</span>
          </div>
        </div>
        <div class="right">
          <svg viewBox="0 0 320 300">
            <defs>
              <linearGradient
                inkscape:collect="always"
                id="linearGradient"
                x1="13"
                y1="193.49992"
                x2="307"
                y2="193.49992"
                gradientUnits="userSpaceOnUse"
              >
                <stop
                  style="stop-color:#ff00ff;"
                  offset="0"
                  id="stop876"
                />
                <stop
                  style="stop-color:#ff0000;"
                  offset="1"
                  id="stop878" 
                />
              </linearGradient>
            </defs>
            <path d="m 40,120.00016 239.99984,-3.2e-4 c 0,0 24.99263,0.79932 25.00016,35.00016 0.008,34.20084 -25.00016,35 -25.00016,35 h -239.99984 c 0,-0.0205 -25,4.01348 -25,38.5 0,34.48652 25,38.5 25,38.5 h 215 c 0,0 20,-0.99604 20,-25 0,-24.00396 -20,-25 -20,-25 h -190 c 0,0 -20,1.71033 -20,25 0,24.00396 20,25 20,25 h 168.57143" />
          </svg>
          <div class="form">
            <label for="email">Name</label>
            <input @focus="nameFocus" v-model="name" type="text" id="name" placeholder="youName" >
            <label for="password">Password</label>
            <input @focus="passwordFocus" v-model="password" placeholder="941030" type="password" id="password">
            <input @focus="submitFocus" type="submit" id="submit" value="Submit" @click="login(name, password)">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Message } from 'element-ui';
import anime from 'animejs'
import { mapState } from 'vuex'

export default {
  computed: mapState({
  }),
  data() {
    return {
      current: null,
      name: '',
      password: '',
      count: 0,
    }
  },
  methods: {
    // email
    nameFocus() {
      if (this.current) {
        this.current.pause();
      } 
      this.current = anime({
        targets: 'path',
        strokeDashoffset: {
          value: 0,
          duration: 700,
          easing: 'easeOutQuart'
        },
        strokeDasharray: {
          value: '240 1386',
          duration: 700,
          easing: 'easeOutQuart'
        }
      });
    },

    // password
    passwordFocus() {
      if (this.current) {
        this.current.pause();
      }
      this.current = anime({
        targets: 'path',
        strokeDashoffset: {
          value: -336,
          duration: 700,
          easing: 'easeOutQuart'
        },
        strokeDasharray: {
          value: '240 1386',
          duration: 700,
          easing: 'easeOutQuart'
        }
      });
    },

    // submit
    submitFocus() {
      if (this.current){
        this.current.pause();
      } 
      this.current = anime({
        targets: 'path',
        strokeDashoffset: {
          value: -730,
          duration: 700,
          easing: 'easeOutQuart'
        },
        strokeDasharray: {
          value: '530 1386',
          duration: 700,
          easing: 'easeOutQuart'
        }
      });
    },

    // login
    login(name, password) {
      if (name == 'yangXiuTing' && password == '941030') {
        this.$router.push({name: 'animation'});
      } else {
        this.count ++;
        if (this.count >= 5) {
          // setTimeout(() => {
          //   this.cssSelector = anime({
          //     targets: `#text`,
          //     translateY: 350,
          //     opacity: 1,
          //   });
          // }, 100);
          return Message({
            type: 'error',
            message: 'yangXiuTing and 941030',
          })
        }
        if (name != 'yangXiuTing') {
          Message({
            type: 'error',
            message: '用户名不对，或许可以试试驼峰命名'
          })
        } else {
          Message({
            type: 'error',
            message: '密码不对，可能写在提示框里。'
          })
        }
      }
    }
    
  },
  mounted() {
    this.$nextTick(() => {
      
    })
  },
}
</script>

<style>
    /* body {
      background: white;
      font-family: 'Inter UI', sans-serif;
      margin: 0;
      padding: 20px;
    } */
</style>

<style lang="less" scoped>
*{
  box-sizing: content-box;
}
input {
  padding: initial;
}

  @import url('https://rsms.me/inter/inter-ui.css');
  ::selection {
    background: #2D2F36;
  }
  ::-webkit-selection {
    background: #2D2F36;
  }
  ::-moz-selection {
    background: #2D2F36;
  }
  .page {
    background: #e2e2e5;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    // height: calc(~ "100% - 40px");
    height: 100vh;
    position: absolute;
    place-content: center;
    width: 100%;
    // width: calc(~ "100% - 40px");
  }
  @media (max-width: 767px) {
    .page {
      // height: auto;
      // margin-bottom: 20px;
      padding-bottom: 20px;
    }
  }
  .container {
    display: flex;
    height: 320px;
    margin: 0 auto;
    width: 640px;
  }
  @media (max-width: 767px) {
    .container {
      flex-direction: column;
      height: 630px;
      width: 320px;
    }
  }
  .left {
    background: white;
    height: calc(~ "100% - 40px");
    top: 20px;
    position: relative;
    width: 50%;
  }
  @media (max-width: 767px) {
    .left {
      height: 100%;
      left: 20px;
      width: calc(~ "100% - 40px");
      max-height: 270px;
    }
  }
  .login {
    font-size: 50px;
    font-weight: 900;
    margin: 50px 40px 40px;
  }
  .eula {
    color: #999;
    font-size: 14px;
    line-height: 1.5;
    margin: 40px;
  }
  .right {
    background: #474A59;
    box-shadow: 0px 0px 40px 16px rgba(0,0,0,0.22);
    color: #F1F1F2;
    position: relative;
    width: 50%;
  }
  @media (max-width: 767px) {
    .right {
      flex-shrink: 0;
      height: 100%;
      width: 100%;
      max-height: 350px;
    }
  }
  svg {
    position: absolute;
    width: 320px;
  }
  path {
    fill: none;
    stroke: url(#linearGradient);;
    stroke-width: 4;
    stroke-dasharray: 240 1386;
  }
  .form {
    margin: 40px;
    position: absolute;
  }
  label {
    color:  #c2c2c5;
    display: block;
    font-size: 14px;
    height: 16px;
    margin-top: 20px;
    margin-bottom: 5px;
  }
  input {
    background: transparent;
    border: 0;
    color: #f2f2f2;
    font-size: 20px;
    height: 30px;
    line-height: 30px;
    outline: none !important;
    width: 100%;
  }
  input::-moz-focus-inner { 
    border: 0; 
  }
  #submit {
    color: #707075;
    margin-top: 45px;
    transition: color 300ms;
  }
  #submit:focus {
    color: #f2f2f2;
  }
  #submit:active {
    color: #d0d0d2;
  }
</style>



